import React, { CSSProperties, useRef, useEffect, useState, useContext } from "react";
import { Card, Header, Grid,  Empty as EmptyEl, Loader, Badge } from "../../style";
import Filter from "../filter";
import { ComponentsContext } from "../../context/components";
import { primaryColor } from "../../config";
import { useMetricData } from "../../hooks/metrics";
import { LineChart as ILineChart } from "../../model/domain/dashboard";
import { LineChart } from "./chart";
import { overflow } from "../../utils/text";

interface Editable {
  editable: boolean;
  remove: () => void;
}

const isWhite = (color: string) => (color === 'white' || color.toLowerCase() === '#ffffff');

const containerHeight = 300;
export const ChartWithControl = (props: ILineChart & Editable) => {
    const { editable, remove } = props;

    const { backgroundColor, title, timeSeriesQuery } = props;
    const type = timeSeriesQuery.crossSeriesReducer === 'NONE'? 'ALL' : timeSeriesQuery.crossSeriesReducer;
    const color = isWhite(backgroundColor) || true ? primaryColor: '#ffffff';

    const [containerWidth, setContainerWidth] = useState(300);
    const { editMode } = useContext(ComponentsContext);

    const { metricDataStatus, interval, setInterval } = useMetricData(timeSeriesQuery);
  
    //parent of the content
    const container = useRef<any>(null);

    const content = (containerWidth < 100)
      ? <Empty />
      : metricDataStatus.loading
      ? <Loading />
      : metricDataStatus.error
      ? <Error />
      : !metricDataStatus.data!.timeSeriesResult
      ? <DataMissing />
      : <LineChart
          backgroundColor={backgroundColor}
          height={containerHeight}
          width={containerWidth}
          timeSeriesResult={metricDataStatus.data!.timeSeriesResult}
          metricDescriptor={metricDataStatus.data!.metricDescriptor} />

    const resize = () => {
      const { clientWidth: width } = container.current;
      setContainerWidth(width);
    };

    useEffect(resize, [container?.current]);

    useEffect(() => {
      window?.addEventListener("resize", resize);
      return () => window.removeEventListener("resize", resize);
    }, []);

    return (
      <Card
        placeSelf="stretch"
        shadow
        grid
        customCols={"1fr auto"}
        bgColor={backgroundColor}
        gridGap={"0 1em"}
        className="slide-up noselect"
      >
        {
          <Header
            className="grid"
            color={color}
            style={{
              gridColumn: "1/-1",
              width: "100%",
              gridTemplateColumns: "1fr auto auto",
              placeItems: "center start",
            }}
            bold
            size={4}
          >
            <Grid mobileLayout={false} customCols="auto auto" gridGap=".5em" padding=".2em .5em"
              style={{
                borderRadius: "1em",
                textTransform: "capitalize",
                placeSelf: "start",
              }}
            >
            
              <Header bold size={3.75}>{overflow(title, 25)}</Header>
              <Badge bold>{type}</Badge>
            </Grid>
            {editMode? (
              <Grid
                gridGap="0"
                placeItems="stretch"
                cols="2"
                className="ant-dropdown-link rounded shadow"
                style={{
                  background: editable ? "white" : "gray",
                  fontSize: "1.35em",
                  border: "2px solid white",
                }}
              >
                <div
                  className={editable ? "hover-txt" : ""}
                  style={{
                    color: !editable ? "white" : "teal",
                    borderRight: `2px solid`,
                    padding: ".1em .5em",
                  }}
                >
                  <i className="fa fa-edit"></i>
                </div>
                <div
                  className={editable ? "hover-txt" : ""}
                  onClick={() => (editable ? remove() : {})}
                  style={{
                    color: !editable ? "white" : "red",
                    padding: ".1em  .5em",
                  }}
                >
                  <i className="far fa-trash-alt"></i>
                </div>
              </Grid>
            ):
            <Filter 
              interval={interval} 
              backgroundColor={'white'}
              color={color} 
              loading={metricDataStatus.error}
              hide={editMode}
              setInterval={setInterval}
            />}
          </Header>
        }
        <div
          style={{ width: "100%", marginTop: "1em" }}
          ref={container}>
          <div style={{height: `${containerHeight}px`, width: containerWidth + 'px'}}>
            {content}
          </div>
        </div>
      </Card>
    );
};

const d100: CSSProperties = {height: '100%', width: '100%', textAlign: 'center', lineHeight: '100px'};

const Empty = () => <EmptyEl text="Screen too Small"/>;

const Loading = () => (
  <div className="grid" style={{...d100}}>
    <div>
      <Loader.Spinner />
      <Header bold>Loading..</Header>
    </div>
  </div>
);

const Error = () => (
  <div style={{...d100}}><EmptyEl text="Error Occured" style={{marginTop: "1em"}} /></div>
);

const DataMissing = () => (
  <div style={{...d100}}> <EmptyEl text="No Data Found" style={{marginTop: "1em"}} /></div>
);